.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  &__top {
    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 8px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    line-height: 27px;
    color: $dark-grey;
    text-decoration: none;
  }

  &__book-a-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $dark-grey;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    color: $dark-grey;
    text-decoration: none;
  }
}
