.about-us {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
      margin-bottom: 12px;
    }

    @include onDesktop {
      grid-column: 6 / 12;
      margin-bottom: 20px;
    }
  }

  &__description {
    margin: 0;
    font-size: 23px;
    font-weight: 500;
    line-height: 140%;
  }
}
